// general styles for both side bars
.full-width{
  .side-bar-container{
    display: none;
  }
}

.side-bar-container{
  flex-direction:column;
  display: flex;
  align-items:stretch;
  height: 560px;
  width: 27.5vw;
  flex: 1 auto;
  order: 1;
  overflow: scroll;
  margin: auto;
  ul {
    list-style: none;
    padding-left: 0px;
  }
  
  a {
    color: $main-dark-blue;
  }
  
  .event-count {
    color: $light-gray;
    text-align: center;
    margin-bottom: 0px;
    margin-top: 10px;
    padding: 10px;
    .town-hall {
      color: $main-light-blue;
      font-weight: 700;
    }
    .grassroots {
      color: $main-dark-blue;
      font-weight: 700;
    }
  }

  #groups-list, #events-list {
    flex:auto 2;
    order: 2;
    margin: auto;
    margin: 10px;
  }

  .ant-card-head{
    padding-left: 10px;
    padding-right: 10px;
  }

  .ant-card-head-title {
    .title {
      white-space: initial;
      @include montserrat-bold-all-caps;
    }
  }
}

@media all and (max-width: $tablet-breakpoint) {
  .side-bar-container {
    flex: 1 100%;
  }
}
